
.login{  
   margin: 0;
   padding: 0;
    height: 100vh;
    background:linear-gradient(120deg,#2980b9,#8e44ad);
    overflow: hidden;
    font-family: montserrat;
}
.logincontainer{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 350px;
    background-color: white;
    border-radius: 20px;
}
.logincontainer h1{
    text-align: center;
    padding: 10px 0 20px 0;
    border-bottom: 2px solid silver;
}
.logincontainer form{
    padding: 0 40px;
     margin: 0;
}
.logincontainer form .inputfields{
    position: relative;
    border-bottom: 2px solid #adadad;
    margin: 30px 0;
}
.inputfields input
{
    width: 100%;
    padding: 0 5px;
    height: 40px;
    font-size: 16px;
    border: none;
    outline: none;
}
.inputfields label{
      position: absolute;
      top: 50%;
      left: 5px;
      color: #adadad;
      transform: translateY(-50%);
      font-size: 16px;
      pointer-events: none;
     
}
.inputfields span::before{
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #2691d9;
} 
 .inputfields input:focus~label,
  .inputfields input:valid~label{
    top: 2px;
    color: #2691d9;
} 
 .logincontainer input[type="submit"]{
    width: 100%;
    border-radius: 25px;
    height: 40px;
    font-size: 16px;
    background: linear-gradient(120deg,#2980b9,#8e44ad);
}
.logincontainer form .signup{
     padding: 20px;
     text-align: center;
     font-size: 16px;
}